            data-bs-dismiss="modal"
          >
            新增
          </button>
          <button
            class="btn btn-primary"
            v-if="isEditedStatus"
            @click="editedMerchandiseData"
            data-bs-dismiss="modal"
          >
            編輯
          </button>
