<script>
import bus from "../EventBus.js";
export default {
  data() {
    return {
      // data-bs-dismiss="modal"
      objectMerchandiseData: {
        merchandiseName: "", // 商品名稱
        roleName: "", // 角色名稱
        merchandiseOriginalPrice: "", // 商品原價
        merchandiseMiscellaneous: "", // 商品雜費
        merchandiseTotalPrice: "", // 商品總價
        merchandiseSource: "", // 商品來源
        merchandiseShoppingPlatform: "", // 商品購入平台
        merchandiseNote: "", // 商品備註
        merchandisePurchaseDate: "", // 購入日期
      },
      tempToday: "",
      isEditedStatus: false,
    };
  },
  methods: {
    // emit 把新增的資料傳到 home.vue 顯示, 並清除資料
    addMerchandiseData() {
      bus.emit("addMerchandiseData", this.objectMerchandiseData);
      console.log("emit");

      this.deleteModalData();
    },
    // 檢視商品詳細資訊
    checkMerchandiseData(merchandiseData) {
      this.objectMerchandiseData = Object.assign({}, merchandiseData);
      console.log(this.objectMerchandiseData);
      this.isEditedStatus = true;
    },
    // 編輯完成 emit 回去, 清資料
    editedMerchandiseData() {
      bus.emit("editedMerchandiseData", this.objectMerchandiseData);
      this.deleteModalData();
    },
    // 新增或編輯或關閉時, 清除 input 資料並補上日期 (日期沒要清除), 編輯按鈕=>新增按鈕
    deleteModalData() {
      Object.keys(this.objectMerchandiseData).forEach(
        (k) => delete this.objectMerchandiseData[k]
      );
      this.objectMerchandiseData.merchandisePurchaseDate = this.tempToday;
      this.isEditedStatus = false;
    },
  },
  created() {
    let now = new Date();
    //格式化"日"，如果小於9，前面補0
    let day = ("0" + now.getDate()).slice(-2);
    //格式化"月"，如果小於9，前面補0
    let month = ("0" + (now.getMonth() + 1)).slice(-2);
    let today = now.getFullYear() + "-" + month + "-" + day;
    this.tempToday = today;
    this.objectMerchandiseData.merchandisePurchaseDate = today;
    // 監聽 點擊 info 傳送商品資訊的 emit 事件, 然後執行 checkMerchandiseData() 顯示資料
    bus.on("clickMerchandiseData", this.checkMerchandiseData);
  },
};
</script>
